import React from "react";
import styled from "styled-components";
import {Grid} from "@material-ui/core";
import {TokenInterFace} from "../../../web3/token.config";
import {FormatAddr} from "../../../utils/format_addr";
import Dimg from "../../../assets/images/d@2x.png"
import Eimg from "../../../assets/images/e@2x.png"
import {AppConfig} from "../../../config";
import {useWallet} from "use-wallet";

export const ReceiveTis: React.FC<{ tokenItem: TokenInterFace, receive:string}> = ({tokenItem,receive}) => {
    return (
        <ReceiveTisStyle>
            <h4>You will receive</h4>
            <GridWidget name={tokenItem.stakeName + ' dToken'} type={Dimg} logo={tokenItem.logo} receive={receive} adds={tokenItem.dToken}/>
            <div style={{height:'18px'}} />
            <GridWidget name={ tokenItem.stakeName +' eToken'} type={Eimg} logo={tokenItem.logo} receive={receive}  adds={tokenItem.eToken}/>
        </ReceiveTisStyle>
    );
}


export  const GridWidget: React.FC<{ name: string, type?: string, logo: string, adds:string, receive:string }> = ({name, type, logo,adds,receive}) => {
    const {chainId} = useWallet();

    return (<GridWidgetStyle>
        <a  href={AppConfig.etherscan[chainId || 42] + 'address/' +adds}>
            <div className='left'>
                <img src={logo} className={'tokenList'} alt=""/>
                {type&&<img src={type} className={'icon'} alt=""/>}
            </div>
            <div className={'right'}>
                <h3>{name}: {receive}</h3>
                <h5>{FormatAddr(adds)}</h5>
            </div>
        </a>


    </GridWidgetStyle>);
}

const GridWidgetStyle = styled(Grid)`
  >a{
    display: flex;
    text-decoration: none;
  }

  .left {
    width: 36px;
    height: 36px;
    position: relative;
    .icon{
      display: block;
      width: 15px;
      position: absolute;
      bottom: 0;
      right: 0;
    }
    .tokenList {
      display: block;
      width: 100%;
    }

    margin-right: 5px;
  }

  .right {
    h3 {
      font-size: 16px;
      font-weight: 500;
      color: #181C2B;
    }

    h5 {
      font-size: 12px;
      font-weight: 400;
      color: rgba(0, 117, 255, 0.7);
      line-height: 15px;
    }
  }
`

const ReceiveTisStyle = styled.div`
  background-color: #EDF2F6;
  padding: 17px;
  margin: 24px 0;
  border-radius: 8px;

  h4 {
    font-size: 14px;
    font-weight: 400;
    color: #51516E;
    line-height: 18px;
    margin-bottom: 15px;
  }

`
