import {Contract} from "web3-eth-contract";
import Web3 from "web3";
import {ContractMap} from "./token.config";

export class Contrcts {
    public sunderContract: Contract;
    public chefContract: Contract;
    public rewardContract: Contract;
    public requestAllContract: Contract;
    public sVaultContract: Contract;

    public sunderAddress: string;
    public chefAddress: string;
    public rewardAddress: string;
    public requestAllAddress: string;
    public sVaultAddress: string;


    constructor(web3: Web3, networkId: number,) {

        this.sunderAddress = ContractMap.SunderContract[networkId];
        this.chefAddress = ContractMap.ChefContract[networkId];
        this.rewardAddress = ContractMap.RewardContract[networkId];
        this.requestAllAddress = ContractMap.RequestAll[networkId];
        this.sVaultAddress = ContractMap.SVault[networkId];

        this.sunderContract = new web3.eth.Contract(ContractMap.SunderContract.abi, this.sunderAddress);
        this.chefContract = new web3.eth.Contract(ContractMap.ChefContract.abi, this.chefAddress);
        this.rewardContract = new web3.eth.Contract(ContractMap.RewardContract.abi, this.rewardAddress);
        this.requestAllContract = new web3.eth.Contract(ContractMap.RequestAll.abi, this.requestAllAddress);
        this.sVaultContract = new web3.eth.Contract(ContractMap.SVault.abi, this.sVaultAddress);
    }
}





