import React, {useCallback, useContext, useEffect} from "react";
import {Grid, Hidden} from "@material-ui/core";
import styled from "styled-components";
import {NavLink} from "react-router-dom";
import logoImg from "../assets/images/sunder_logo@2x.png"
import {useModel} from "../hook/use_model";
import {Alert} from "@material-ui/lab";
import handImg from "../assets/images/hand.png"
import {useWeb3} from "../provider/web3_provider";
import {useWallet} from "use-wallet";
import {FormatAddr} from "../utils/format_addr";
import {getBalanceNumber} from "../utils/formatBalance";
import {useUpDataBalance} from "../state/tokenList/hooks";
import {useSelector} from "react-redux";
import {AppState} from "../state";

export const HeaderWidget: React.FC = () => {
    const listToken = useSelector((state: AppState) => state.tokenList.listToken);
    const upDataBalance = useUpDataBalance();
    const {blockNumber} = useWeb3();
    useEffect(() => {
        upDataBalance(listToken);
    }, [blockNumber])
    return (
        <HeaderStyle>
            <Grid container alignItems={"center"} justify={"space-between"}>
                <Grid item md={2} xs={12}>
                    <LogoWidget/>
                </Grid>
                <Grid item md={5} xs={12}>
                    <NavWidget/>
                </Grid>
                <Hidden smDown>
                    <Grid item>
                        <WalletWidget/>
                    </Grid>
                </Hidden>


            </Grid>
        </HeaderStyle>

    );
}

const WalletWidget: React.FC = () => {
    const {account, balance} = useWallet();
    return (
        <WalletStyle>
            <p>{getBalanceNumber(balance)} ETH</p>
            <p>{FormatAddr(account)}</p>
        </WalletStyle>
    );
}

const LogoWidget: React.FC = () => {
    return (
        <LogoStyle>
            <img src={logoImg} alt=""/>

        </LogoStyle>
    );
}


const NavWidget: React.FC = () => {
    const [onShow] = useModel(<Alert severity="info">Coming Soon</Alert>);
    return (
        <NavStyle>
            <StyledLink to="/main">Mint</StyledLink>
            <StyledLink to="/redeem">Redeem</StyledLink>
            <StyledLink to="/stake">Liquidity Rewards</StyledLink>
            {/*<StyledLink to="/vote">Vote</StyledLink>*/}
            <StyledLink to="/value">Earn Yield</StyledLink>
            <SomiStyle target='_blank' href={'https://vote.sunder.finance/#/'}>Vote</SomiStyle>
            {/*<SomiStyle onClick={()=>onShow()}>Governance</SomiStyle>*/}

            {/*<StyledLink to="/test">test</StyledLink>*/}
        </NavStyle>
    );
}

