import React, {createContext, useContext} from "react";
import {Contract} from "web3-eth-contract";
import BigNumber from "bignumber.js";


export const EarnContext = createContext<EarnInterface>({
    address: '',
    earnAddr: '',
    contract: undefined,
    deposited: new BigNumber(0),
    balance: new BigNumber(0),
    tokenNick: '1',
    priceShare: new BigNumber(0),
});

export interface EarnInterface {
    address: string,
    earnAddr: string,
    contract: Contract | any,
    tokenNick: string,
    deposited: BigNumber,
    balance: BigNumber,
    priceShare: BigNumber,
}


export const useEarnContext = () => {
    let data = useContext(EarnContext);
    return data;
}
