import React from "react";
import styled from "styled-components";
import {Accordion, AccordionDetails, AccordionSummary, Grid} from "@material-ui/core";
import {DepositedInput} from "./widget/deposited_input";
import ExpandMoreIcon from "@material-ui/icons/ExpandMore";
import {useWeb3} from "../../provider/web3_provider";
import {TokenInterFace} from "../../web3/token.config";
import {ApproverInput} from "./widget/approver_input";
import {useGetTokneAddress} from "../../server/sunder";
import {EarnContext} from "../../provider/earn.provider";
import {useBalance} from "../../utils/token_server";
import {useWallet} from "use-wallet";
import {getBalanceFormat} from "../../utils/formatBalance";
import {useGetPricePerFullShare} from "../../server/rarn";
import {WidthStyle} from "../../widget/style/width_style";
import {useGetTokenMap} from "../../state/tokenList/hooks";

export const ValuePage: React.FC = () => {
    const tokenMap = useGetTokenMap();
    return (
        <div style={{paddingTop: "20px"}}>
            {
                Object.keys(tokenMap).map((ev) => {
                    if (!tokenMap[ev].dpid) return;
                    return <CellWidget model={tokenMap[ev]}/>;
                })
            }
        </div>
    );
}


const CellWidget: React.FC<{ model: TokenInterFace }> = ({model}) => {
    const tokens = useGetTokneAddress(model.address);
    const {contrcts} = useWeb3();
    const {account} = useWallet();
    const balance = useBalance(model.eContract, account ?? '');
    const deposited = useBalance(contrcts.sVaultContract, account ?? '');
    const priceShare = useGetPricePerFullShare();
    return (
        <EarnContext.Provider value={{
            address: tokens.token1,
            deposited,
            priceShare,
            contract: model.dContract,
            earnAddr: model.dToken,
            balance,
            tokenNick:  model.stakeName + ' eToken'
        }}>
            <WapStyle>
                <AccordionStyle>
                    <AccordionSummary
                        aria-controls="panel1bh-content"
                        id="panel1bh-header"
                        expandIcon={<ExpandMoreIcon/>}
                    >
                        <CellTopStyle>
                            <div className={'label'}>
                                <img src={model.logo} alt=""/>
                                <span>{model.stakeName} eToken</span>
                            </div>
                            <div className={'inner'}>
                                <h2><p>{getBalanceFormat(balance)}</p><span>{model.stakeName} eToken</span></h2>
                                <h3>Balance</h3>
                            </div>
                            <div className={'inner'}>
                                <h2><p>{getBalanceFormat(deposited)}</p><span>{model.stakeName} eToken Shares</span></h2>
                                <h3>Deposited</h3>
                            </div>
                            <div className={'inner'}>
                                <h2><p>0</p><span>%</span></h2>
                                <h3>APY</h3>
                            </div>
                        </CellTopStyle>
                    </AccordionSummary>
                    <AccordionDetails>
                        <CellBottomStyle container alignItems={"center"}>
                            <Grid item md={7}>
                                <ApproverInput model={model}/>
                                <DepositedInput model={model}/>
                            </Grid>
                            <Grid item md={5}>
                                <h3 className={'tis'}>
                                    <p>In order to be eligible for participating in </p>
                                    <p>interest bearing strategies for obtaining yields from underlying strategies, the process is structured as follows:</p>
                                    <p>Deposit {model.stakeName} eTokens into the sunder Earn Vault. You will receive a pool share token in your wallet representing ownership share over the obtained revenue.</p>

                                    <p>You will receive a pool share token in your wallet.</p>
                                    A withdrawal of your {model.stakeName} eTokens will automatically include the acquired yields.
                                </h3>
                            </Grid>
                        </CellBottomStyle>
                    </AccordionDetails>
                </AccordionStyle>

            </WapStyle>
        </EarnContext.Provider>

    );
}

const AccordionStyle = styled(Accordion)`
  &.MuiPaper-elevation1 {
    box-shadow: none;
  }
`
const WapStyle = styled.div`
  padding: 20px 0;
  box-sizing: border-box;
  ${() => WidthStyle.MinWidth};
  max-width: 980px;
  margin: 0 auto 24px auto;
  border-radius: 8px;

  .MuiPaper-root {
    background: transparent;
  }

  .MuiAccordionSummary-root {
    padding: 0 20px;
  }

  .MuiButtonBase-root, .MuiAccordionSummary-root.Mui-expanded {
    background: white;
    border-radius: 10px;
    height: 78px;

    .MuiButtonBase-root {
      background: transparent;
    }
  }

  .MuiAccordionDetails-root {
    padding-left: 0;
    padding-right: 0;
  }

  @media (max-width: ${({theme}) => theme.sm}) {
    .MuiButtonBase-root, .MuiAccordionSummary-root.Mui-expanded {
      height: 240px;
    }
  }
`

