import {
    FormControl,
} from "@material-ui/core";
import styled from "styled-components";
import {SelectWidget} from "../../../widget/slect_tokens/select";
import {InputWidget} from "../../../widget/base/input";
import {useForm} from "react-hook-form";
import {ButtonWidget} from "../../../widget/base/button";
import {ApproveButton} from "../../../widget/base/approve_button";
import {useWeb3} from "../../../provider/web3_provider";
import React, {useCallback, useEffect, useMemo, useState} from "react";
import {useGetTokenMin, useLocks, useRedeem} from "../../../server/sunder";
import {getBalanceFormat, getBalanceNumber, getMaxNumber} from "../../../utils/formatBalance";
import {useSendModal} from "../../../widget/modal/send_modal";
import BigNumber from "bignumber.js";
import {TokenInterFace} from "../../../web3/token.config";
import {TitleTextWidget} from "../../../widget/base/title";
import {WidthStyle} from "../../../widget/style/width_style";
import {useGetTokenMap} from "../../../state/tokenList/hooks";
import {RedeemTisWidget} from "./redeem_tis";
import {ReceiveTis} from "../../mint/widget/receive_tis";


export const RightsWidget: React.FC = () => {
    const {register, errors, handleSubmit, setValue} = useForm();

    const {contrcts} = useWeb3();
    let spenderAddress = contrcts.sunderAddress;
    const _tokenMap = useGetTokenMap();

    const [key, setKey] = useState(Object.keys(_tokenMap)[0]);

    let tokenItem: TokenInterFace = _tokenMap[key];

    const [amountMax, setAmountMax] = useState(0);

    // 获取 两个token balance min
    const {min: amount, dToken, eToken} = useGetTokenMin(tokenItem.address);
    const isLock = useLocks(tokenItem.address);

    const headerToken = useCallback(async () => {
        setAmountMax(getBalanceNumber(amount));
    }, [tokenItem, amount]);

    // 执行redeem 方法
    const [facethConvert] = useRedeem();

    const sendFn = useSendModal(facethConvert);

    // 提交请求
    const handleLogin = useCallback(async (data: any) => {
        let _number = getMaxNumber(data['amount'], new BigNumber(amount));
        await sendFn(tokenItem.address, _number);
    }, [tokenItem, tokenItem, amount]);

    useEffect(() => {
        headerToken();
    }, [amount])

    return (
        <RightStyle>
            <div className="wap">
                <TitleTextWidget showH3={true} text={"Redeem"} title={'Step by step process:'} arrText={[
                    `When you redeem tokens, the protocol will take your dTokens and eTokens and redeem them for the Native token. Every Native token is made up with one dToken and one eToken.`,
                    `Enter the amount of Native Governance tokens you would like to redeem, while the interface below will display the conversion rate of dTokens needed for redemption. You can also directly press the 'max' button to automatically redeem your maximum capacity.`,
                    `Approve the contract and confirm, to execute the redemption process.`,
                ]}/>
                <div className="inner">
                    <p className={'stake'}>Redeem native governance tokens by combining dTokens and eTokens</p>
                    <p className={'charged'}>0.1% fee charged upon redemption</p>

                    <div className={'input'}>
                        <FormControl id="demo-simple-select-label" fullWidth={true}>
                            <SelectWidget
                                value={key}
                                setValue={setKey}
                            />
                        </FormControl>
                    </div>
                    <div className={'input'}>
                        <InputWidget errors={errors}
                                     inputRef={register({
                                         required: 'Please enter a number > 0',
                                         max: {value: amountMax, message: `amountMax ${amountMax}`}
                                     })}
                                     sunTitle={
                                         <div>
                                             {/*<p>Collateral: <a href={tokenItem.netWork}>{tokenItem.nick}</a></p>*/}
                                         </div>
                                     }
                                     label={'Amount to redeem'}
                                     name={'amount'}
                                     placeholder={'0.00'}
                                     setValue={(ev) => {
                                         setValue(ev, amountMax)
                                     }}
                        >
                            <div className={'balance'}>Balance: <span>{amountMax}</span></div>
                        </InputWidget>
                        <div className={'bottoms'}>
                            {tokenItem && <RedeemTisWidget
                                tokenItem={tokenItem}
                                dToken={getBalanceFormat(dToken,2)}
                                eToken={getBalanceFormat(eToken,2)}
                                receive={`${amountMax}`}
                            />}

                            {/*<p className={'left'}>You have {getBalanceFormat(dToken)} {tokenItem.stakeName} dTokens and {getBalanceFormat(eToken)} Compound eToken, and will receive {amountMax} {tokenItem.stakeName} Native token</p>*/}

                        </div>
                    </div>
                    <ApproveButton contract={tokenItem.contract} contractAddress={tokenItem.address}
                                   spender={spenderAddress}>
                        <ButtonWidget text={'Confirm'} isDisable={isLock} onClick={handleSubmit(handleLogin)}/>
                    </ApproveButton>
                </div>
            </div>


        </RightStyle>
    );
}


const RightStyle = styled.div`
  ${() => WidthStyle.MinInnerStyle};

  .wap {
    background-size: 100% 100%;
  }

  .inner {
    padding: 24px;
    box-sizing: border-box;
  }

  .stake {
    font-size: 16px;
    font-weight: 400;
    color: #696190FF;

    display: flex;
    justify-content: space-between;
  }

  .charged {
    font-size: 14px;
    margin-top: 16px;
    font-weight: 400;
    color: ${({theme}) => theme.color.oranges[100]};
    margin-bottom: 20px;
  }

  .input {
    margin-top: 20px;
    //padding-right: 20px;
    box-sizing: border-box;


    .bottoms {
      //display: flex;
      justify-content: space-between;
      font-size: 14px;
      font-weight: 400;
      margin-bottom: 40px;
      margin-top: 30px;

      .left {
        color: ${({theme}) => theme.color.oranges[100]};
      }

      a {
        color: #2F35E5;
      }
    }


  }

  .balance {
    color: rgb(211, 211, 211);
    width: 200px;
  }
`
