import {useDispatch, useSelector} from "react-redux";
import {AppDispatch, AppState} from "../index";
import {useCallback, useEffect, useState} from "react";
import {addToken, deleteToken, initTokenList, upDataBalance} from "./actions";
import {ProjectConfig, TokenInterFace} from "../../web3/token.config";
import BigNumber from "bignumber.js";
import {useWallet} from "use-wallet";
import {useWeb3} from "../../provider/web3_provider";
import {CUSTOMKEY, deleteStorage, upDateStorage} from "../../utils/localstorage";
import {Sunder} from "../../web3/sunder";


export const useInitTokenList = () => {
    const dispatch = useDispatch<AppDispatch>();
    return useCallback((tokenMap: { [key: string]: TokenInterFace }) => {
        dispatch(initTokenList({tokenMap}));
    }, []);
}


export const useDeleteToken = () => {
    const dispatch = useDispatch<AppDispatch>();
    return useCallback((tokenKey: string) => {
        deleteStorage(CUSTOMKEY, tokenKey);
        dispatch(deleteToken({tokenKey}));
    }, []);
}


export const useAddToken = (sunder: Sunder) => {
    const dispatch = useDispatch<AppDispatch>();
    const handleAdd = useCallback((modal: ProjectConfig) => {
        const {address} = modal;
        let data: any = {};
        data[address] = modal;
        upDateStorage(CUSTOMKEY, data);
        data['balance'] = new BigNumber(0);
        const item: TokenInterFace = sunder.customProject(modal);
        dispatch(addToken({tokenKey: address, tokenItem: item}));
    }, []);
    return handleAdd;
}


export const useGetTokenMap = (official?:boolean) => {

    return useSelector((state: AppState) => state.tokenList.listToken);
}



export const useUpDataBalance = () => {
    const dispatch = useDispatch<AppDispatch>();
    const {account} = useWallet();
    return useCallback(async (listToken) => {
        console.log(listToken);
        for (const key in listToken) {
            const item: TokenInterFace = listToken[key];
            let balance = new BigNumber(await item.contract.methods.balanceOf(account).call());
            dispatch(upDataBalance({balance, valueKey: key}))
        }
    }, [],);
}


export const useTokenItem = (tokenKey: string): TokenInterFace | undefined => {
    const listToken = useSelector((state: AppState) => state.tokenList.listToken);
    const [Item, setItem] = useState<TokenInterFace>(listToken['usdc']);
    const {account} = useWallet();

    useEffect(() => {
        setItem(listToken[tokenKey])
    }, [listToken, tokenKey, account])
    return Item;
}

export const useTokenBalance = (blockNumber: number, tokenKey: string) => {
    const [balance, setBalance] = useState(new BigNumber(0));
    const listToken = useSelector((state: AppState) => state.tokenList.listToken);
    const {account} = useWallet();

    const handerBalance = useCallback(async () => {
        console.log(listToken)
        let balance = new BigNumber(await listToken[tokenKey].contract.methods.balanceOf(account).call());
        setBalance(balance);
    }, [listToken, blockNumber, tokenKey])
    useEffect(() => {
        if (listToken[tokenKey] == null) return;
        handerBalance();
    }, [blockNumber, tokenKey])
    return balance;
}


