import {useWeb3} from "../provider/web3_provider";
import {useSendFn} from "../utils/web3_send";
import {useCallback} from "react";


export const useRequestAll = () => {
    const {contrcts, web3} = useWeb3();
    const handleSendFn = useSendFn(contrcts.requestAllAddress);
    const facethBack = useCallback(async () => {

        let method = contrcts.requestAllContract.methods.requestAll();
        let txHash = await handleSendFn(method);
        return txHash.transactionHash
    }, [
        contrcts,
        web3
    ])
    return [facethBack]
}
