import {AppConfig} from "../config";

export const CUSTOMKEY = 'customProject';

export const setStorage = (key: string, data: { }) => {
    localStorage.setItem(keyFormat(key), JSON.stringify(data));
}

export const getStorage = (key: string) => {
    const data = localStorage.getItem(keyFormat(key));
    return data ? JSON.parse(data) : null;
}

export const removeStorage = (key: string) => {
    localStorage.removeItem(keyFormat(key));
}

export const upDateStorage = (key: string, data: {}) => {
    const _data = getStorage(key)
    localStorage.setItem(keyFormat(key), JSON.stringify({...data, ..._data}));
    return {...data, ..._data};
}

export const deleteStorage = (key: string, deleteKey:string) => {
    const _data = getStorage(key);
    delete _data[deleteKey]
    localStorage.setItem(keyFormat(key), JSON.stringify({..._data}));
    return _data;
}


function keyFormat(key: string) {
    return 'networkId' + AppConfig.chainId + key;
}
