import {createAction} from '@reduxjs/toolkit'
import {ProjectConfig, TokenInterFace} from "../../web3/token.config";
import BigNumber from "bignumber.js";


export const initTokenList = createAction<{ tokenMap: { [key: string]: TokenInterFace } }>('tokenList/initTokenList');
export const addToken = createAction<{ tokenItem: TokenInterFace, tokenKey: string  }>('tokenList/addToken');
export const deleteToken = createAction<{ tokenKey: string }>('tokenList/deleteToken');

export const upDataBalance = createAction<{ balance: BigNumber,valueKey:string }>('tokenList/upDataBalance');

