import React, {useCallback, useContext, useEffect, useMemo, useState} from "react";
import {ButtonWidget} from "../../../widget/base/button";
import {WapStyle} from "../../../widget/slect_tokens/select_page";
import {useWeb3} from "../../../provider/web3_provider";
import {CircularProgress, createStyles, InputBase, makeStyles, Modal, Theme} from "@material-ui/core";
import SearchIcon from "@material-ui/icons/Search";
import {ProjectConfig} from "../../../web3/token.config";
import styled from "styled-components";
import {HttpStatus} from "../../../utils/wait_fn";
import {useCheckAvailable, useCreatePair} from "../../../server/sunder";
import NoImg from '../../../assets/images/no_img.png'
import {useSendModal} from "../../../widget/modal/send_modal";
import {useAddToken, useDeleteToken, useGetTokenMap} from "../../../state/tokenList/hooks";
import {ModalContext} from "../../../provider/model_provider";

function getModalStyle() {
    const top = 50;
    const left = 50;
    return {
        top: `${top}%`,
        left: `${left}%`,
        transform: `translate(-${top}%, -${left}%)`,
    };
}

const useStyles = makeStyles((theme: Theme) =>
    createStyles({
        paper: {
            position: 'absolute',
            width: 400,
        },
    }),
);

export const CreatePairWidget: React.FC = () => {


    const [open, setOpen] = React.useState(false);
    const handleClose = () => {
        setOpen(false);
    };
    const handleToggle = () => {
        setOpen(!open);
    };
    return (
        <div>
            <ButtonWidget text={'Create Pair'} onClick={() => handleToggle()}/>
            <Modal
                open={open}
                onClose={handleClose}
                aria-labelledby="simple-modal-title"
                aria-describedby="simple-modal-description"
            >
                <CreatePage/>
            </Modal>
        </div>
    );
}


export const CreatePage: React.FC = () => {
    const customProjectMap =  useGetTokenMap();

    const [select, setSelect] = useState('');
    const {createPair, data, status} = useCheckAvailable();

    const requestCreate = useCallback(async () => {
        await createPair(select);
    }, [createPair, select, data, status]);

    const [modalStyle] = React.useState(getModalStyle);
    const classes = useStyles();

    const deleteProtoduct = useDeleteToken();

    useEffect(() => {
        if (!select) return;
        let timeOut = setTimeout(requestCreate, 500);
        return () => clearInterval(timeOut);
    }, [select])

    return (
        <WStyle style={modalStyle} className={classes.paper}>
            <WapStyle>
                <h1>代币</h1>
                <div className={'search'}>
                    <div className={status === HttpStatus.failure ? 'search_err' : ''}>
                        <InputBase fullWidth={true}
                                   placeholder={"Search for ticker or contract address"}
                                   onChange={(ev) => {
                                       setSelect(ev.target.value);
                                   }}
                        />
                        <SearchIcon/>
                    </div>

                </div>
                {
                    status !== HttpStatus.start && <div className={'show_data'}>
                        {status === HttpStatus.wait && <div className={'loading'}>
                            <CircularProgress color="inherit"/>
                        </div>}
                        {status === HttpStatus.failure && <div className={'err'}>{data.err}</div>}
                        {/*{status === HttpStatus.success && <ResultWidget model={data}/>}*/}
                    </div>

                }
                <div className="body">
                    {
                        Object.keys(customProjectMap).map((ev) => {
                            if(customProjectMap[ev].dpid) return;
                            return <CellWidget cb={() => {
                                deleteProtoduct(ev);
                            }} cell={customProjectMap[ev]}/>
                        })
                    }
                </div>
            </WapStyle>
        </WStyle>
    );
}

export const CellWidget: React.FC<{ cell: ProjectConfig, cb: Function }> = ({cell, cb}) => {

    return (
        <CellStyle onClick={() => cb()}>
            <img src={cell.logo} alt=""/>
            <div className={'text'}>
                <h2>{cell.stakeName}</h2>
                <p>{cell.nick}</p>
            </div>
            <div onClick={() => cb()}>删除</div>
        </CellStyle>
    );
}


export const ResultWidget: React.FC<{ model: any,setValue:Function, }> = ({model,setValue}) => {
    const {
        nick,
        stakeName,
        isCreate,
        address,
    } = model;
    const {nsuer} = useWeb3();
    const handleAdd = useAddToken(nsuer);

    const createPair = useCreatePair();
    const sendCreatePair = useSendModal(createPair);
    const customProjectMap =  useGetTokenMap();
    const {onDismiss} = useContext(ModalContext);

    let createButton = (
        <div>
            <ButtonWidget text={'Sunder'}  onClick={() => sendCreatePair(address)}/>
        </div>
    );
    const selectButton = useMemo(() => {
        if (customProjectMap[address]) {
            return <div>

                <ButtonWidget text={'Choose'}  onClick={() => {
                    setValue(address);
                    onDismiss();
                }} />

            </div>;
        }
        return <div>
            <ButtonWidget text={'Add'}  onClick={() => handleAdd(model)} />
        </div>;
    }, [model,customProjectMap,onDismiss])
    return (<ResultStyle>
        <img src={NoImg} alt=""/>
        <div className={'msg'}>
            <h2>{stakeName}</h2>
            <h3>{nick}</h3>
        </div>
        <div className={'button'}>
            {isCreate ? selectButton : createButton}
        </div>
    </ResultStyle>);
}

