import {TokenInterFace} from "../web3/token.config";
import {useWallet} from "use-wallet";
import {useCallback, useEffect, useMemo, useState} from "react";
import {ethers} from 'ethers'
import {useSendFn} from "../utils/web3_send";
import {useWeb3} from "../provider/web3_provider";
import {Contract} from "web3-eth-contract";
import BigNumber from "bignumber.js";
import {getBalanceFormat} from "./formatBalance";
import ERC20ABI from "../web3/abi/erc20.json";


export const useApprove = (contract: Contract | undefined, contractAddress: string, spender: string) => {

    const handleSendFn = useSendFn(contractAddress);

    const handleApprove = useCallback(async () => {
        if (!contract) return;
        try {
            let method = contract.methods.approve(spender, ethers.constants.MaxUint256,);

            let data = await handleSendFn(method);

            return data.transactionHash;
        } catch (e) {
            throw e
        }

    }, [contract, spender,contractAddress]);
    return [handleApprove];
}


export const useAllowance = (contract: Contract | undefined, spender: string) => {
    const [allowance, setAllowance] = useState(0);
    const {account,} = useWallet();
    const {blockNumber} = useWeb3();
    const fetchAllowance = useCallback(async () => {
        if (!contract) return;
        const allowance = await contract.methods
            .allowance(account, spender)
            .call();
        setAllowance(allowance)
    }, [account, contract, spender]);

    useEffect(() => {
        fetchAllowance();
    }, [blockNumber,contract,spender]);
    return allowance;
}


export const useBalance = (ERC20: Contract | undefined,dafAccount?:string) => {
    const [allowance, setAllowance] = useState(new BigNumber(0));
    const {account} = useWallet();
    const {blockNumber} = useWeb3();
    const fetchAllowance = useCallback(async () => {
        if (!ERC20) return;
        const allowance = await ERC20.methods.balanceOf(dafAccount||account).call();
        setAllowance(new BigNumber(allowance));
    }, [account, dafAccount,ERC20,allowance]);

    useEffect(() => {
        fetchAllowance();
    }, [blockNumber,ERC20]);
    return allowance;
}

// 使用erc20 地址查看余额
export const useAddrBalance = (ercAddr: string | undefined,dafAccount?:string) => {
    const [allowance, setAllowance] = useState(new BigNumber(0));
    const {account} = useWallet();
    const {blockNumber, web3} = useWeb3();
    const fetchAllowance = useCallback(async () => {
        if (!ercAddr) return;
        const ERC20 = new web3.eth.Contract(ERC20ABI.abi as Array<any>, ercAddr);

        const allowance = await ERC20.methods.balanceOf(dafAccount||account).call();
        setAllowance(new BigNumber(allowance));
    }, [account, dafAccount,allowance]);

    useEffect(() => {
        fetchAllowance();
    }, [blockNumber,ercAddr]);
    return allowance;
}


//get erc20 合约
export const useGetERC20 = (ercAddr: string | undefined) => {
    const [ERC20, setERC20] = useState();
    const {account} = useWallet();
    const {blockNumber, web3} = useWeb3();
    const fetchAllowance = useCallback(async () => {
        if (!ercAddr) return;
        const _erc20 = new web3.eth.Contract(ERC20ABI.abi as Array<any>, ercAddr);

        setERC20(_erc20);
    }, [account]);

    useEffect(() => {
        fetchAllowance();
    }, []);
    return ERC20;
}




