import {WapWidget} from "../../../widget/base/wap";
import React from "react";
import styled from "styled-components";
import {TopCellWidget} from "../widget/top_cell_widget";
import {

    useParams
} from "react-router-dom";
import {Grid} from "@material-ui/core";
import {VoteDetailsRight} from "../widget/vote_details_left";
import {VoteDetailsLeft} from "../widget/vote_details_right";
import {listData} from "./vote_home";
export const VoteDetailsPage: React.FC = () => {
    let  slug:{id:string} = useParams();
    return (
        <WapWidget isBack={'/vote/index'} title={'Back to Proposals'}>
            <TopCellWidget model={listData[parseInt(slug.id)]}/>
            <MainStyle container>
                <VoteDetailsLeft />
                <VoteDetailsRight model={listData[parseInt(slug.id)]} />

            </MainStyle>
        </WapWidget>
    );
}


const MainStyle = styled(Grid)`
  border-top: 1px solid #E7E9ED;
  margin-top: 20px;
  padding-top: 20px;
  
`
