import {SelectTopWidget} from "../../widget/slect_tokens/select_top";
import {getBalanceFormat, getBalanceNumber, getMaxNumber} from "../../utils/formatBalance";
import {ButtonWidget} from "../../widget/base/button";
import React, {useCallback} from "react";
import styled from "styled-components";
import {WidthStyle} from "../../widget/style/width_style";
import {useStakeContext} from "../../provider/stake_provider";
import {TokenInterFace} from "../../web3/token.config";
import {useWeb3} from "../../provider/web3_provider";
import {useHistory} from "react-router-dom";
import {useDeposit, useHarvest, useUserInfo, useWithdraw} from "../../server/chef";
import {useAddrBalance, useBalance} from "../../utils/token_server";
import {StakeButtonWidget} from "./widget/button";
import {useSendModal} from "../../widget/modal/send_modal";
import BigNumber from "bignumber.js";
import {useGetTokenMap} from "../../state/tokenList/hooks";

export const PreviewWidget: React.FC<{ showHref: boolean }> = ({showHref}) => {

    const {totalDeposited, key, pid, tokenNick, contract, address,lpAddr} = useStakeContext();
    const  tokenMap = useGetTokenMap();
    const tokenItem: TokenInterFace = tokenMap[key];
    const {amount, deposits,} = useUserInfo(pid);
    const balance = useAddrBalance(lpAddr);


    const [facethHarvest] = useHarvest();
    const sendHarvest = useSendModal(facethHarvest);

    // 提交请求
    const [facethWithdraw] = useWithdraw();
    const sendWithdraw = useSendModal(facethWithdraw);
    const handleWithdraw = useCallback(async (data: string) => {
        let _number = getMaxNumber(data, amount);
        return  sendWithdraw(pid, _number)
    }, [pid, amount],);


    // 提交请求
    const [facethDeposit] = useDeposit();

    const sendDeposit = useSendModal(facethDeposit);
    const handleDeposit = useCallback(async (amount: string) => {
        let _number = getMaxNumber(amount, new BigNumber(balance));
        return sendDeposit(pid, _number);
    }, [balance, pid]);

    return (
        <WapStyle>

            <MainStyle>
                <div className="wap">
                    <LogoWidget name={tokenNick} img={tokenItem.logo}/>
                    <CellWidget s1={'N/A'} s2={'Pool APY'}/>
                    <CellWidget s1={getBalanceFormat(totalDeposited)} s2={'Total Deposited'}/>
                    <CellWidget s1={getBalanceFormat(amount)} s2={'My Deposits'} img={true}/>
                    <CellWidget s1={`${getBalanceFormat(deposits)} Sunder`} s2={'My Rewards'}>
                        <ButtonWidget isDisable={deposits.eq(0)} onClick={() => sendHarvest(pid)} text={"Claim Rewards"}/>
                    </CellWidget>
                    <div className="button">
                        <StakeButtonWidget
                            handleWithdraw={handleWithdraw}
                            handleDeposit={handleDeposit}
                            amount={amount}
                            maxNumber={balance}
                        />
                    </div>
                    <UniswapStyle showHref={showHref}>
                        <a target="_blank"
                           href={`https://app.sushi.com/add/0x4F96Fe3b7A6Cf9725f59d353F723c1bDb64CA6Aa/${address}`}>Go
                            to Sushiswap Pool</a>
                    </UniswapStyle>
                </div>
            </MainStyle>
        </WapStyle>
    );
}


const CellWidget: React.FC<{ s1: string, s2?: string, img?: boolean }> = ({s1, s2, img, children}) => {
    return (
        <CellStyle>
            <div>
                <h2>{s1}</h2>
                <h3>{s2}</h3>
            </div>
            <div className={'button_a'}>
                {children}
            </div>

        </CellStyle>
    );
}
const LogoWidget: React.FC<{ name: string, img: string }> = ({name, img}) => {
    return (
        <CellStyle>
            <div>
                <h1>
                    <img src={img} alt=""/>
                    <span>{name}</span>
                </h1>
                {/*<h3>Rewards Pool</h3>*/}
            </div>
        </CellStyle>
    );
}

