import {getBalanceFormat} from "../../../utils/formatBalance";
import {DateUtils} from "../../../utils/date";
import BigNumber from "bignumber.js";

export const TopStakeWidget: React.FC<{
    setKey: Function,
    keys: string
}> = ({
          setKey, keys
      }) => {
    return (
        <div>
            <WapStyle container justify={"space-between"}>
                <Grid item md={3} sm={3} xs={12}>
                    <SelectTopWidget keys={keys} setKey={setKey}>{keys}</SelectTopWidget>
                </Grid>
                <Grid item md={9} sm={9} xs={12}>
                    <TopStepWidget/>
                </Grid>
            </WapStyle>
        </div>

    );
}

const TopStepWidget: React.FC = () => {
    const {
        epochId,
        reward,
        startTime,
        endTime,
    } = useStep();
    return (
        <TopStepStyle>
            <div className="inner">
                <div className={'label'}>
                    Epoch {epochId}
                    <span/>
                </div>
                <div className={'cell'}>
                    <h3>Start</h3>
                    <h4>{DateUtils.dataMinuteString(parseInt(startTime) * 1000)}</h4>
                </div>
                <div className={'cell'}>
                    <h3>End</h3>
                    <h4>{DateUtils.dataMinuteString(parseInt(endTime) * 1000)}</h4>
                </div>
                <div className={'cell'}>
                    <h3>Rewards</h3>
                    <h4>{getBalanceFormat(reward,0)} SGT</h4>
                </div>
            </div>
        </TopStepStyle>
    );

}

