import {useWeb3} from "../../provider/web3_provider";
import styled from "styled-components";
import {useModel} from "../../hook/use_model";
import {SelectPage} from "./select_page";
import React from "react";
import {TokenInterFace} from "../../web3/token.config";
import dropDown from "../../assets/images/drop_down.png"
import {useGetTokenMap} from "../../state/tokenList/hooks";
export const SelectTopWidget:React.FC<{setKey:Function,keys:string}> = ({setKey,keys}) => {
    const tokenMap = useGetTokenMap();

    const tokenItem: TokenInterFace = tokenMap[keys];
    const [handlePresent] = useModel(<SelectPage isLocal={false} setValue={setKey}/>);
    return (
        <SelectStyle>
            <p>Protocol</p>
            <span/>
            <h3 onClick={handlePresent}>{tokenItem?.nick} <img src={dropDown} alt=""/></h3>
        </SelectStyle>
    );
}


const SelectStyle = styled.div`
  border-radius: 8px;


  padding: 10px 20px;
  display: flex;
  height: 100%;
  align-items: center;
  box-sizing: border-box;
  background-color: rgba(244, 248, 250, 1);
  p {
    font-size: 18px;
    font-weight: 600;
    color: rgba(24, 28, 43, 1);
    line-height: 31px;
  }

  span {
    display: block;
    background-color: #847DA669;
    width: 2px;
    height: 30px;
    margin: 0 20px;
  }

  h3 {
    color: rgba(81, 81, 110, 1);
    font-size: 14px;
    font-weight: 500;
    display: flex;
    align-items: center;
    justify-content: space-between;
    flex: 1;
    img{
      display: block;
      width: 10px;
    }
  }
  
  @media(max-width: 600px){
    p{
      font-size: 18px;
    }
    span{
      margin: 0 30px 0 20px;
    }
    h3{
      font-size: 16px;
    }
  }
`;
