import React, {useCallback, useContext, useMemo, useState} from "react";
import {Grid, Slider, TextField, Typography} from "@material-ui/core";
import styled from "styled-components";
import {ButtonWidget} from "../../../widget/base/button";
import {StakeButtonWidget} from "../../stake/widget/button";
import {ApproveButton} from "../../../widget/base/approve_button";
import {TokenInterFace} from "../../../web3/token.config";
import {useWeb3} from "../../../provider/web3_provider";
import {ButtonStyle as InputButton, FlexStyle, InputStyle} from "../../../widget/base/input";
import {useEarnContext} from "../../../provider/earn.provider";
import {getBalanceFormat, getBalanceNumber, getMaxNumber} from "../../../utils/formatBalance";
import {useDeposit} from "../../../server/rarn";
import BigNumber from "bignumber.js";
import {SendModal, useSendModal} from "../../../widget/modal/send_modal";
import {ModalContext} from "../../../provider/model_provider";

export const ApproverInput: React.FC<{ model: TokenInterFace }> = ({model}) => {
    const {contrcts} = useWeb3();
    const {balance, tokenNick} = useEarnContext();
    const [_number, setNumber] = useState<any>();
    const {depositAll,deposit} = useDeposit();
    const {onPresent} = useContext(ModalContext);
    let _max = useMemo(()=> {
        return getBalanceNumber(balance);
    } , [balance]);
    const handleDeposit = useCallback(async () => {
        if(_number == _max){
            onPresent(<SendModal confirmationFn={() => depositAll()}/>);
        }else{
            onPresent(<SendModal confirmationFn={() => deposit(getMaxNumber(_number, new BigNumber(balance)))}/>);
        }
    }, [balance,depositAll,_number,deposit]);
    return (
        <div>
            <ValueStyle>
                <h3>Balance: <span>{getBalanceFormat(balance)} {tokenNick}</span></h3>
                <Grid container alignItems={"center"} spacing={4}>
                    <Grid item md={8} xs={12}>
                        <FlexStyle className={'flex_style'}>
                            <InputStyle>
                                <TextField
                                    value={_number}
                                    fullWidth={true}
                                    placeholder={`Amount to deposit`}
                                    onChange={(ev) => {
                                        setNumber(ev.target.value);
                                    }}
                                />
                            </InputStyle>

                            <InputButton onClick={() => {
                                setNumber(_max);
                            }}>Max</InputButton>
                        </FlexStyle>
                    </Grid>

                    <Grid item md={4} xs={12}>
                        <ApproveButton contract={model.eContract} contractAddress={model.eToken}
                                       spender={contrcts.sVaultAddress}>
                            <ButtonWidget isDisable={balance.toFixed() === '0' || !_number} onClick={handleDeposit} text={'Deposit'}/>
                        </ApproveButton>
                    </Grid>
                </Grid>


            </ValueStyle>
        </div>
    );
}


const ValueStyle = styled.div`
  padding: 20px 30px;
  box-sizing: border-box;

  @media(max-width: ${({theme})=> theme.sm}){
    padding: 10px 20px;
    h3{
      span{
        display: block;
        font-size: 14px;
        color: #51516E;
      }
    }
    .flex_style{
      margin: 15px 0 0 0;
    }
  }
  
  .ButtonWidget {
    height: 40px;
  }

  h3 {
    font-size: 18px;

    font-weight: 600;
    color: #000000;
    line-height: 22px;
  }

 
`
