export const useConvert = () => {
    const {contrcts, web3} = useWeb3();
    const handleSendFn = useSendFn(contrcts.sunderAddress);
    const facethBack = useCallback(async (address: string, amount: string) => {

        let method = contrcts.sunderContract.methods.convert(address, amount);
        let txHash = await handleSendFn(method);
        return txHash.transactionHash
    }, [
        contrcts,
        web3
    ])
    return [facethBack]
}

