// 获取两个token
export const useGetTokneAddress = (address: string) => {
    const {contrcts, web3,} = useWeb3();
    const [tokens, setToken] = useState<any>({
        dToken: "",
        eToken: "",
        token1Contract: null,
        token2Contract: null
    });
    const GetTokens = useCallback(async () => {
        console.log(address);
        let _tokens: any = {
            dToken: "",
            eToken: "",
        };

        _tokens['dToken'] = await contrcts.sunderContract.methods.dtokens(address).call();
        _tokens['eToken'] = await contrcts.sunderContract.methods.etokens(address).call();
        _tokens.token1Contract = new web3.eth.Contract(ERC20ABI.abi as Array<any>, _tokens['dToken']);
        _tokens.token2Contract = new web3.eth.Contract(ERC20ABI.abi as Array<any>, _tokens['eToken']);
        setToken(_tokens);
    }, [contrcts,
        web3,
        address]);

    useEffect(() => {
        GetTokens()
    }, [address])

    return tokens;
}

// 获取两个token 中余额最低的数值
export const useGetTokenMin = (address: string) => {
    const {contrcts, web3, blockNumber} = useWeb3();
    const {account} = useWallet();
    const {token1Contract, token2Contract} = useGetTokneAddress(address);
    const [min, setMin] = useState({
        min: 0,
        dToken: 0,
        eToken: 0
    });
    const facethBack = useCallback(async () => {
        if (!token1Contract) return;

        let arr = [];
        arr[0] = await token1Contract.methods.balanceOf(account).call();
        arr[1] = await token2Contract.methods.balanceOf(account).call();

        let min = Math.min.apply(null, arr);

        setMin({
            min,
            dToken: arr[0],
            eToken: arr[1]
        });
    }, [
        contrcts,
        web3,
        address,
        token1Contract,
        token2Contract,
        blockNumber
    ])
    useEffect(() => {
        facethBack();
    }, [blockNumber, token1Contract, address])
    return min
}


export const useCheckAvailable = () => {
    const {contrcts, web3} = useWeb3();
    const [status, setStatus] = useState<HttpStatus>(HttpStatus.start);
    const [data, setData] = useState<any>({});


    const createPair = useCallback(async (adds: string) => {
        setStatus(HttpStatus.wait);
        try {
            if (!web3.utils.isAddress(adds)) {
                setStatus(HttpStatus.failure);
                setData({err: 'Invalid token address'});
                return;
            }
            const erc20 = new web3.eth.Contract(ERC20ABI.abi as Array<any>, adds);

            let _decimals = await erc20.methods.decimals().call();
            if (!_decimals) {
                setStatus(HttpStatus.failure);
                setData({err: 'Invalid token address'});
                return;
            }
            let _name = await erc20.methods.name().call();
            let _symbol = await erc20.methods.symbol().call();
            let _dTokenEToken = await contrcts.sunderContract.methods.dTokenEToken(adds).call();
            let _result: ProjectConfig = {
                logo: "",
                nick: _name,
                stakeName: _symbol,
                dToken: _dTokenEToken[0],
                eToken: _dTokenEToken[1],
                decimals: _decimals,
                address: adds
            };
            if (new BigNumber(_dTokenEToken[0]).toFixed() === '0') {
                setData({..._result, isCreate: false})
            } else {
                setData({..._result, isCreate: true})
            }
            setStatus(HttpStatus.success);
            return;

        } catch (e) {
            setStatus(HttpStatus.failure);
            setData({err: 'Invalid token address'});
            return;
        }
    }, [contrcts, web3,]);

    return {
        status,
        data,
        setStatus,
        createPair,
    };
}


export const useCreatePair = () => {
    const {contrcts} = useWeb3();
    const handleSendFn = useSendFn(contrcts.sunderAddress);

    const createPair = useCallback(async (adds: string) => {
        let method = contrcts.sunderContract.methods.createPair(adds);
        let txHash = await handleSendFn(method);
        return txHash.transactionHash
    }, [contrcts.sunderContract.methods, handleSendFn]);

    return createPair;
}


export const useLocks = (address: string) => {
    const {contrcts} = useWeb3();
    const [isLock, setLock] = useState(false);
    const locks = useCallback(async () => {
        if (contrcts == null) return;
        try {
            console.log(contrcts.sunderContract.methods.locks, address)
            const _value = await contrcts.sunderContract.methods.locks(address).call();
            setLock(_value);
        } catch (e) {
            console.log(e, '<---------err--------useLocks')
        }

    }, [contrcts.sunderContract.methods, address]);

    useEffect(() => {
        locks()
    }, [address])

    return isLock;
}
