import React, {useCallback, useContext, useMemo, useState} from "react";
import {Grid, Slider, TextField, Typography} from "@material-ui/core";
import styled from "styled-components";
import {ButtonWidget} from "../../../widget/base/button";
import {TokenInterFace} from "../../../web3/token.config";
import {ButtonStyle as InputButton, FlexStyle, InputStyle} from "../../../widget/base/input";
import {getBalanceFormat, getBalanceNumber, getMaxNumber} from "../../../utils/formatBalance";
import {SendModal} from "../../../widget/modal/send_modal";
import BigNumber from "bignumber.js";
import {useWithdraw} from "../../../server/rarn";
import {ModalContext} from "../../../provider/model_provider";
import {useEarnContext} from "../../../provider/earn.provider";

export const DepositedInput: React.FC<{ model: TokenInterFace }> = ({model}) => {
    const [_number, setNumber] = useState<any>();
    const {deposited,priceShare,tokenNick} = useEarnContext();
    const {withdrawAll, withdraw} = useWithdraw();
    const {onPresent} = useContext(ModalContext);


    let _max = useMemo(()=> {
        return getBalanceNumber(deposited).toFixed();
    } , [deposited]);

    const handleWithdraw = useCallback(async () => {
        if (_number == _max) {
            onPresent(<SendModal confirmationFn={() => withdrawAll()}/>);
        } else {
            onPresent(<SendModal confirmationFn={() => withdraw(getMaxNumber(_number, new BigNumber(deposited)))}/>);
        }
    }, [deposited, withdrawAll,_number, withdraw]);
    return (
        <div>
            <ValueStyle>
                <h3>Deposited: <span>{getBalanceFormat(deposited)} ≈ {getBalanceFormat(priceShare.times(deposited.div(1e18)))} {tokenNick}</span></h3>
                <Grid container alignItems={"center"} spacing={4}>
                    <Grid item md={8} xs={12}>
                        <FlexStyle className={'flex_style'}>
                            <InputStyle>
                                <TextField
                                    value={_number}
                                    fullWidth={true}
                                    placeholder={`Amount to withdraw`}
                                    onChange={(ev) => {
                                        setNumber(ev.target.value);
                                    }}
                                />

                            </InputStyle>

                            <InputButton onClick={() => {
                                setNumber(_max);
                            }}>Max</InputButton>
                        </FlexStyle>
                    </Grid>

                    <Grid item md={4} xs={12}>
                        <ButtonWidget  onClick={handleWithdraw} isDisable={deposited.toFixed() === '0' || !_number} text={'Withdraw'}/>
                    </Grid>
                </Grid>


            </ValueStyle>
        </div>
    );
}


const ValueStyle = styled.div`
  padding: 20px 30px;
  box-sizing: border-box;

  .ButtonWidget {
    height: 40px;
  }


  @media(max-width: ${({theme})=> theme.sm}){
    padding: 10px 20px;
    h3{
      span{
        display: block;
        font-size: 14px;
        color: #51516E;
      }
    }
    .flex_style{
      margin: 15px 0 0 0;
    }
  }


  h3 {
    font-size: 18px;

    font-weight: 600;
    color: #000000;
    line-height: 22px;
  }




`
