import React, {useState} from "react";
import styled from "styled-components";
import {useWeb3} from "../../provider/web3_provider";
import {useGetTokenMin, useGetTokneAddress} from "../../server/sunder";
import {TokenInterFace} from "../../web3/token.config";
import {SelectTopWidget} from "../../widget/slect_tokens/select_top";
import {WidthStyle} from "../../widget/style/width_style";
import {useAddrBalance, useBalance} from "../../utils/token_server";
import {StakeContext} from "../../provider/stake_provider";
import {PreviewWidget} from "./preview";
import {Grid} from "@material-ui/core";
import {useWallet} from "use-wallet";
import {TopStakeWidget} from "./widget/top_stake";
import {useGetTokenMap} from "../../state/tokenList/hooks";


export const StakePage: React.FC = () => {

    const { contrcts,nsuer} = useWeb3();
    const  tokenMap = useGetTokenMap();

    const [key, setKey] = useState(Object.keys(nsuer.tokenMap)[0]);

    const tokenItem: TokenInterFace = tokenMap[key];

    const balance = useAddrBalance(tokenItem.dTokenDAI, contrcts.chefAddress || '');
    const balancee = useAddrBalance(tokenItem.eTokenDAI, contrcts.chefAddress || '');


    return (
        <WapStyle>
            <TopStakeWidget keys={key} setKey={setKey}/>
            <Grid container spacing={4}>
                <Grid item sm={6} md={6} xs={12}>
                    <StakeContext.Provider value={{
                        address: tokenItem.dToken,
                        totalDeposited: balance,
                        pid: tokenItem.dpid,
                        contract: tokenItem.dContract,
                        lpAddr: tokenItem.dTokenDAI,
                        key, setKey,
                        tokenNick: tokenItem.stakeName + ' dToken/DAI SLP'
                    }}>
                        <PreviewWidget showHref={true}/>
                    </StakeContext.Provider>
                </Grid>
                <Grid item sm={6} md={6} xs={12}>
                    <StakeContext.Provider value={{
                        address: tokenItem.eToken,
                        totalDeposited: balancee,
                        pid: tokenItem.epid,
                        contract: tokenItem.eContract,
                        lpAddr: tokenItem.eTokenDAI,
                        key, setKey,
                        tokenNick: tokenItem.stakeName + ' eToken/DAI SLP'

                    }}>
                        <PreviewWidget showHref={true}/>
                        {/*<WapStyle>*/}
                        {/*    <Switch>*/}

                        {/*        <Route path="/stake/preview" component={PreviewWidget}/>*/}
                        {/*        <Route path="/stake/deposit" component={DepositPage}/>*/}

                        {/*        <Redirect to="/stake/preview"/>*/}
                        {/*    </Switch>*/}

                        {/*</WapStyle>*/}
                    </StakeContext.Provider>
                </Grid>
            </Grid>
        </WapStyle>

    );
}


const WapStyle = styled.div`
  padding-bottom: 20px;
  ${() => WidthStyle.MinWidth};
  max-width: 890px;
`




