import Web3 from "web3";
import {Project, ProjectConfig, TokenInterFace, TokenList} from "./token.config";
import ERC20ABI from './abi/erc20.json'
import {Contrcts} from "./contrcts";
import WalletConnect from "@walletconnect/client";
import {AppConfig} from "../config";
import {CUSTOMKEY, getStorage} from "../utils/localstorage";
import NoImg from "../assets/images/no_img.png";
import React from "react";
import BigNumber from "bignumber.js";

export class Sunder {
    public web3: Web3;
    public tokenMap: { [key: string]: TokenInterFace } = TokenList;
    public customProjectMap: { [key: string]: TokenInterFace } = {};
    public networkId: number;
    public contrcts: Contrcts;
    public walletConnect: WalletConnect | undefined;


    constructor(provider: any, networkId: number, options: any) {

        this.networkId = networkId;
        let realProvider;
        if (typeof provider === 'string') {
            if (provider.includes('wss')) {
                realProvider = new Web3.providers.WebsocketProvider(
                    provider,
                    options.ethereumNodeTimeout || 10000,
                )
            } else {
                realProvider = new Web3.providers.HttpProvider(
                    provider,
                    options.ethereumNodeTimeout || 10000,
                )
            }
        } else {

            if (options.connector === 'walletconnect') {
                realProvider = AppConfig.rpcUrl[networkId];
                this.walletConnect = new WalletConnect({bridge: AppConfig.rpcUrl[networkId],});
            } else {
                realProvider = provider;
            }
        }
        this.web3 = new Web3(realProvider);
        this.contrcts = new Contrcts(this.web3, networkId)
        this.initTokenMap();
        this.initCustomProject();
    }


    initTokenMap() {
        Object.keys(this.tokenMap).map((ev) => {
            const item = this.tokenMap[ev];
            item.address = item.address[this.networkId || 42];
            item.eToken = item.eToken[this.networkId || 42];
            item.dToken = item.dToken[this.networkId || 42];
            item.eTokenDAI = item.eTokenDAI[this.networkId || 42];
            item.dTokenDAI = item.dTokenDAI[this.networkId || 42];
            item.eContract = new this.web3.eth.Contract(ERC20ABI.abi as Array<any>, item.eToken);
            item.dContract = new this.web3.eth.Contract(ERC20ABI.abi as Array<any>, item.dToken);
            item.contract = new this.web3.eth.Contract(ERC20ABI.abi as Array<any>, item.address);

        });
    }

    initCustomProject() {
        const allCustomProject = getStorage(CUSTOMKEY);
        if (allCustomProject == null) return;
        Object.keys(allCustomProject).map((ev) => {
            const item = allCustomProject[ev];
            this.customProjectMap[ev] = this.customProject(item);
        });
        return this.customProjectMap;
    }


    customProject(item: ProjectConfig): TokenInterFace {
        const _cellModel: TokenInterFace = {
            contract: new this.web3.eth.Contract(ERC20ABI.abi as Array<any>, item.address),
            dContract: new this.web3.eth.Contract(ERC20ABI.abi as Array<any>, item.dToken),
            eContract: new this.web3.eth.Contract(ERC20ABI.abi as Array<any>, item.eToken),

            dTokenDAI: "",
            dpid: "",
            eTokenDAI: "",
            epid: "",
            netWork: "",
            ...item,
            logo: NoImg,
        };

        return _cellModel;
    }


}
