import React, {useMemo} from "react";
import styled from "styled-components";
import {TokenInterFace} from "../../web3/token.config";
import {formatAddress} from "../../utils/formatBalance";
import {AppConfig} from "../../config";
import {useWallet} from "use-wallet";
import {useModel} from "../../hook/use_model";
import {SelectPage} from "./select_page";

import NoImg from "../../assets/images/no_img.png";
import {useGetTokenMap} from "../../state/tokenList/hooks";

export const SelectWidget: React.FC<{

    value: string, setValue: (key: string) => void
}> =
    ({ value, setValue}) => {
        const listToken = useGetTokenMap();
        const {chainId} = useWallet();

        const [handlePresent] = useModel(<SelectPage isLocal={true} value={value}  setValue={setValue}/>);

        return (
            <SelectStyle>

                <div className={'top'}>
                    <p className={'left'}>Vault</p>
                </div>
                <MainStyle onClick={handlePresent}>
                    <div>
                        <img
                            src={listToken[value]?.logo ?? NoImg}
                            alt=""/>
                        <p>
                            {listToken[value].stakeName} ({listToken[value].nick})
                        </p>
                    </div>

                </MainStyle>
                <div className={'bottom'}>
                    <div>
                        <p>Token Contract: </p>
                        <a href={AppConfig.etherscan[chainId || 42] + 'address/' + listToken[value].address}>{formatAddress(listToken[value].address)}</a>
                    </div>
                </div>

            </SelectStyle>

        );
    }


const SelectStyle = styled.div`
  .label {
    border: 1px solid ${({theme}) => theme.color.blues[200]};
    border-radius: 8px;
    margin: 15px 0;
  }


  .MuiInput-underline:after, .MuiInput-underline:before {
    display: none;
  }

  .top {
    display: flex;
    justify-content: space-between;
    align-items: baseline;

    font-weight: 400;

    .left {
      font-size: 16px;
      font-weight: 600;
      color: #2A1D69FF;
      line-height: 1;
    }

    div {
      display: flex;
      font-size: 14px;
      line-height: 1;

      color: ${({theme}) => theme.color.blacks[100]};

      p {
        display: block;
        margin-right: 5px;
      }

      a {
        color: ${({theme}) => theme.color.blues[100]};
      }
    }
  }

  .bottom {
    display: flex;
    justify-content: flex-end;

    div {
      display: flex;
      font-size: 12px;
      line-height: 1;
      align-items: center;
      font-weight: 400;
      color: #2A1D69FF;

      p {
        display: block;
        margin-right: 5px;
      }

      a {
        color: ${({theme}) => theme.color.greens[100]};
        text-decoration: none;
        font-size: 10px;
      }
    }
  }

  @media (max-width: ${({theme}) => theme.sm}) {
    .top {
      .left {
        font-size: 14px;
      }
    }
  }

`


const MainStyle = styled.div`
  display: flex;
  justify-content: space-between;
  align-items: center;
  border: 1px solid ${({theme}) => theme.color.blues[200] + 'aa'};

  border-radius: 8px;
  margin: 15px 0;
  box-sizing: border-box;

  > div {
    display: flex;
    align-items: center;

    p {
      line-height: 1;
      color: #2A1D69FF;

    }
  }

  padding: 6px 20px;

  img {
    display: block;
    width: 30px;
    margin-right: 10px;
  }
`

