import {useWeb3} from "../provider/web3_provider";
import {useSendFn} from "../utils/web3_send";
import {useCallback, useEffect, useState} from "react";
import {useWallet} from "use-wallet";
import BigNumber from "bignumber.js";

export const useDeposit = () => {
    const {contrcts, web3} = useWeb3();
    const handleSendFn = useSendFn(contrcts.chefAddress);
    const facethBack = useCallback(async (pid: number, amount: string) => {
        let method = contrcts.chefContract.methods.deposit(pid, amount);
        let txHash = await handleSendFn(method);
        return txHash.transactionHash
    }, [
        contrcts,
        web3
    ])
    return [facethBack]
}

