import React, {useEffect} from 'react';
import './App.css';
import {HashRouter as Router, Switch, Route, Redirect,} from "react-router-dom";
import {HeaderWidget} from "./widget/header";
import {MainPage} from "./view/mint/mint";
import {RedeemPage} from "./view/redeem/redeem";
import {useWallet, UseWalletProvider} from 'use-wallet'
import {AppConfig} from "./config";
import {useWeb3, Web3Provider} from "./provider/web3_provider";
import {ModelProvider} from "./provider/model_provider";
import {BackdropProvider} from "./provider/backdrop_provider";
import {WalletConnect, WalletConnectTis} from "./widget/modal/wallet_connect";
import styled, {ThemeProvider} from "styled-components";
import {StakePage} from "./view/stake/stake";
import {VoteIndexPage} from "./view/vote/vote_inder";
import {ValuePage} from "./view/value/value";
import {theme} from "./theme";
import store from "./state";
import {Provider} from "react-redux";
import {useUpDataBalance} from "./state/tokenList/hooks";

function App() {
    return (
        <Providers>
            <Page/>
        </Providers>

    );
}

const Providers: React.FC = ({children}) => {
    return (
        <UseWalletProvider chainId={AppConfig.chainId} connectors={{
            walletconnect: {rpcUrl: AppConfig.rpcUrl[AppConfig.chainId]}
        }}>
            <ThemeProvider theme={theme}>
                <Provider store={store}>
                    <Web3Provider>
                        <ModelProvider>
                            <BackdropProvider>
                                {children}
                            </BackdropProvider>
                        </ModelProvider>
                    </Web3Provider>
                </Provider>

            </ThemeProvider>
        </UseWalletProvider>

    );
}


const Page: React.FC = () => {

    const {status} = useWallet();



    return (<PubBg>
        {
            status === 'connected' ? <Router>
                <HeaderWidget/>
                <PageStyle>
                    <Switch>
                        <Route path="/main" component={MainPage}/>
                        <Route path="/redeem" component={RedeemPage}/>
                        <Route path="/stake" component={StakePage}/>
                        <Route path="/vote" component={VoteIndexPage}/>
                        <Route path="/value" component={ValuePage}/>
                        <Redirect to="/main"/>
                    </Switch>

                </PageStyle>
            </Router> : <div
                style={{
                    alignItems: 'center',
                    display: 'flex',
                    flex: 1,
                    justifyContent: 'center',
                    height: '100vh',
                    background: "linear-gradient(314deg, #FAF6F5 0%, #EFEEFA 32%, #E2E9EF 100%)"
                }}
            >
                <WalletConnect/>

            </div>

        }
    </PubBg>);
};


const PubBg = styled.div`
`
const PageStyle = styled.div`
  position: initial;
  min-height: 85vh;
`

export default App;
